ast, what constitute our present view, and what ideas on developments and challenges do we possess for the future. Furthermore, I would like to provide some real-world examples, in my perspective best practices, as an inspiration."),_c('br')])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/stan_geertman.jpg"),"width":"160px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: "),_c('br'),_vm._v("Prof. Dr. Stan Geertman (1955) is Professor of Planning Support Science at Utrecht University, the Netherlands. He has published widely in both national and international journals and has published a range of (editorial) books. He is a member of the editorial board of several scientific journals, including CEUS (Computers, Environment, and Urban Systems). He has been member and/or chair of several international conference organizations (EGIS; JECC; AGILE-2011; DDSS; CUPUM-2013) and was up till recently Chair of the international Board of Directors of CUPUM (Computers in Urban Planning and Urban Management). He has supervised a range of PhD students and Postdocs and participated in a diversity of interdisciplinary research projects, ranging from ‘Climate change and sustainable land use’ up till ‘Spatial economic deconcentration’ and ‘Floodlabel’. He has been scientific director for 10 years of a spin-off of Utrecht University (Nexpri) and was one of the founding fathers and first program director of the inter-university (TUD, WUR, UU, ITC-UT) Master program GIMA (Geographical Information Management and Applications). For two years he was appointed scientific advisor of the Management Team of the PGM-Department at ITC, Twente University. Since 2016 he is Chair of Spatial Planning within the Department of Human Geography an Planning of the Faculty of Geosciences at Utrecht University. His research interest: "),_c('ul',[_c('li',[_vm._v("Planning Support Science (PSScience)")]),_c('li',[_vm._v("Smart Cities and Smart Governance")]),_c('li',[_vm._v("Sustainable urbanization, notably in Western and Chinese contexts")])]),_c('br')])],1)],1),_c('table',{attrs:{"width":"100%"}},[_c('tr',{staticClass:"tituloPalestra"},[_c('th',{staticClass:"bold"},[_vm._v("PALESTRA MAGNA "),_c('br'),_vm._v(" 07/04/2021 – 11h15")])])]),_c('v-container',[_c('v-row',{staticClass:"bold",staticStyle:{"text-align":"left"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v(" Os desafios para aplicação da Nova Agenda Urbana nas cidades brasileiras"),_c('br'),_vm._v(" Daniel Todtmann Montandon"),_c('br'),_vm._v(" Diretor de Arquitetura e Urbanismo na Universidade Nove de Julho ")])],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v("O Programa das Nações Unidas para os Assentamentos Humanos - ONU Habitat - tem fortalecido a agenda mundial relacionada ao desenvolvimento urbano sustentável. Além das grandes conferências realizadas a cada 20 anos, tem realizado os Fóruns Urbanos Mundiais a cada 2 anos e na Conferência Habitat III, realizada em Quito, Equador, em outubro de 2016, instituiu a Nova Agenda Urbana, que consiste na principal referência internacional de orientação para o desenvolvimento urbano sustentável. Cabe, portanto, examinar o teor desse documento e em que medida está sendo de fato implementado pelas cidades brasileiras. ")])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/daniel_todtmann.jpg"),"width":"170px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: "),_c('br'),_vm._v("Arquiteto pela Universidade Estadual Paulista, Unesp/Bauru (2001). Mestre em Planejamento Urbano e Regional pela Faculdade de Arquitetura e Urbanismo da Universidade de São Paulo - FAUUSP (2009). Doutorando na mesma universidade, tendo iniciado em 2017. Foi Professor de Urbanismo na Escola da Cidade de janeiro de 2013 a dezembro de 2020 e na Universidade São Judas Tadeu de agosto de 2018 a dezembro de 2019. Foi Diretor de Planejamento Urbano no Ministério das Cidades (2008 a 2011). Na Secretaria Municipal de Desenvolvimento Urbano da Prefeitura da Cidade de São Paulo foi Diretor do Departamento de Uso do Solo (2013 a 2016), Presidente da Comissão de Proteção à Paisagem Urbana - CPPU (2013 a 2015), presidente da Câmara Técnica de Legislação Urbanística - CTLU (2015 a 2016) e representante titular da secretaria no Conselho Municipal de Política Urbana - CMPU (2015 a 2016). Foi responsável pela coordenação da revisão da lei de zoneamento (Lei 16.402/16). Participou de diversos projetos de cooperação técnica internacional sobre planejamento urbano com a Organização das Nações Unidas (ONU), Aliança de Cidades, Agência de Cooperação Internacional do Japão (JICA) e Lincoln Institute of Land Policy. É autor, co-autor e organizador de publicações sobre instrumentos de planejamento urbano e Estatuto da Cidade. Atualmente exerce a função de Diretor de Arquitetura e Urbanismo na Universidade Nove de Julho. "),_c('br'),_c('br')])],1)],1),_c('table',{attrs:{"width":"100%"}},[_c('tr',{staticClass:"tituloPalestra"},[_c('th',{staticClass:"bold"},[_vm._v("PALESTRA MAGNA "),_c('br'),_vm._v(" 08/04/2021 – 10h")])])]),_c('v-container',[_c('v-row',{staticClass:"bold",staticStyle:{"text-align":"left"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v(" Inequality in Transport David Banister"),_c('br'),_vm._v(" Emeritus Professor of Transport Studies at the School of Geography and the Environment (SoGE) and the Transport Studies Unit of University of Oxford ")])],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v("Everyone needs transport to move around and to access everyday needs, but for each individual those needs are different, and they change over time and space: herein lie the seeds of inequalities in transport. This complex problem will be explored, first through an understanding of inequality, its nature, measurement and extent. The empirical part of the presentation addresses inequality and transport through a detailed analysis of the variations in daily and long-distance travel in Great Britain over a ten-year period. It is argued that there must be a much wider interpretation of inequality– one that links actual travel with measures of wellbeing and sustainability, recognizing that these will change over time. Conclusions are drawn for policy makers and planners, and public policy more generally. "),_c('br'),_vm._v(" The details are David Banister (2018) Inequality in Transport – Price £30.00 – p. 272 and in full colour. ISBN 978-0-906661-01-7 "),_c('br'),_vm._v(" See the book website - "),_c('a',{attrs:{"href":"www.inequalityintransport.org.uk","target":"_blank"}},[_vm._v(" www.inequalityintransport.org.uk")]),_vm._v(" – there is lots of information there and samples of what is in the book - it is available through a variety of outlets, including amazon. "),_c('br'),_vm._v(" It is also available through amazon kindle "),_c('a',{attrs:{"href":"https://www.amazon.co.uk/dp/B07F2QMS6X/","target":"_blank"}},[_vm._v(" https://www.amazon.co.uk/dp/B07F2QMS6X/")]),_vm._v(" price £9.99. ")])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/david_banister.jpg"),"width":"210px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: "),_c('br'),_vm._v("Professor Emeritus of Transport Studies at Oxford University. From 2006-2015 he was the Director of the Transport Studies Unit, Oxford University and he was Director of the Environmental Change Institute, also at Oxford University (2009-2010). Prior to 2006, he was Professor of Transport Planning at University College London. He has spent time as a Research Fellow at the Warren Centre in the University of Sydney (2001-2002), a Visiting VSB Professor at the Tinbergen Institute in Amsterdam (1994-1997) and Visiting Professor at the University of Bodenkultur in Vienna in 2007. He was the first Benelux BIVEC-GIBET Transport Chair (2012-2013) and he was recently the Chair of the ERC Advanced Research Grants SH3 Panel (2015-2016). His research expertise is in transport scenario building, sustainable cities, energy and environmental modelling, and he has published 25 books and over 300 papers in refereed journals on these topics. "),_c('br'),_c('br')])],1)],1),_c('table',{attrs:{"width":"100%"}},[_c('tr',{staticClass:"tituloPalestra"},[_c('th',{staticClass:"bold"},[_vm._v("PALESTRA MAGNA "),_c('br'),_vm._v(" 08/04/2021 – 11h")])])]),_c('v-container',[_c('v-row',{staticClass:"bold",staticStyle:{"text-align":"left"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v(" Clima e Planejamento urbano"),_c('br'),_vm._v(" Eleonora Sad de Assis"),_c('br'),_vm._v(" Arquiteta e Urbanista, com ênfase em Adequação Ambiental e professora da Universidade Federal de Minas Gerais ")])],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v("As estruturas urbanas produzem uma alteração local do balanço de energia de superfície, causando uma mudança no clima que pode influenciar toda uma região, dependendo do tamanho da cidade. Os vários aspectos do chamado clima urbano são hoje bem conhecidos e podem ser organizados nos canais de percepção humana: conforto térmico, qualidade do ar e impacto hidrometeórico (precipitações). Tais aspectos podem ser acentuados pelos efeitos das mudanças climáticas globais no território da cidade, demandando medidas de combate aos efeitos adversos do clima urbano, que são de longo prazo. Assim sendo, tais medidas devem integrar os planos diretores municipais e mesmo, os planos de ação climática dos estados e municípios."),_c('br'),_vm._v(" Como não há soluções genéricas, principalmente num país com a diversidade climática do Brasil, pretende-se colocar em discussão certas similaridades (urbanas) e diferenças (climáticas) que poderiam conduzir a soluções locais efetivas, a partir da aplicação de métodos e técnicas já disponíveis da pesquisa em clima urbano. ")])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/eleonora_sad.jpeg"),"width":"160px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: "),_c('br'),_vm._v("Graduada em Arquitetura e Urbanismo pela Universidade Federal de Minas Gerais (1985), possui mestrado em Arquitetura pela Universidade Federal do Rio de Janeiro (1990) e doutorado em Arquitetura e Urbanismo pela Universidade de São Paulo (2000). Atualmente é Professora Associada IV da Universidade Federal de Minas Gerais. Tem experiência na área de Arquitetura e Urbanismo, com ênfase em Adequação Ambiental, atuando principalmente nos seguintes temas: conforto ambiental, eficiência energética, conforto térmico, clima urbano, planejamento e planejamento urbano. "),_c('br'),_c('br')])],1)],1),_c('table',{attrs:{"width":"100%"}},[_c('tr',{staticClass:"tituloPalestra"},[_c('th',{staticClass:"bold"},[_vm._v("PALESTRA SETORIAL "),_c('br'),_vm._v(" 09/04/2021 – 08h30 – Sala 1")])])]),_c('v-container',[_c('v-row',{staticClass:"bold",staticStyle:{"text-align":"left"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v(" Experiências de elaboração de políticas públicas para o turismo"),_c('br'),_vm._v(" Renata Maria Ribeiro"),_c('br'),_vm._v(" Professora Assistente da Universidade Estadual Paulista Júlio de Mesquita Filho – UNESP ")])],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v("As políticas públicas de Turismo se apresentam no Brasil em vários momentos, entretanto, a partir da década de 1990 se caracterizam por uma continuidade. No Estado de São Paulo a Lei 12.061/2015 provocou uma nova conduta aos gestores de municípios turísticos ou que possuem potencialidade para desenvolver a atividade. Em nossa palestra vamos conversar sobre os Planos de Desenvolvimento Turístico realizados em algumas cidades e sua contribuição para transformar recursos culturais e naturais, em produto turístico. ")])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/renata_maria.jpg"),"width":"170px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: "),_c('br'),_vm._v("Graduada em Turismo pela Universidade Federal do Paraná (1993), Mestre em Geografia pela Universidade Federal do Paraná (2005) e Doutora em Geografia pela Universidade Federal do Paraná (2011) na área de Planejamento Urbano e Regional. Atualmente é professora assistente doutora da Universidade Estadual Paulista Júlio de Mesquita Filho - UNESP. Leciona e pesquisa nas áreas de Planejamento Territorial do Turismo, Turismo no espaço urbano, Planejamento e políticas públicas de turismo, hospitalidade e turismo, planejamento e plano diretor de cidades para o turismo. Realizou os Planos Diretores desenvolvimento Turístico de Panorama -SP, Pirapozinho - SP, Salmourão - SP entre outros. "),_c('br'),_c('br')])],1)],1),_c('table',{attrs:{"width":"100%"}},[_c('tr',{staticClass:"tituloPalestra"},[_c('th',{staticClass:"bold"},[_vm._v("PALESTRA SETORIAL "),_c('br'),_vm._v(" 09/04/2021 – 08h30 – Sala 2")])])]),_c('v-container',[_c('v-row',{staticClass:"bold",staticStyle:{"text-align":"left"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v(" Como oito cidades catarinenses desenvolveram juntas seus planos de mobilidade"),_c('br'),_vm._v(" Andrea Holz Pfutzenreuter, Simone Becker Lopes e Renata Cavion"),_c('br'),_vm._v(" Professoras da Universidade Federal de Santa Catarina – UFSC ")])],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_c('br'),_vm._v("Quais ações municipais são necessárias para viabilizar a elaboração dos Planos de Mobilidade Urbana (PMU)? As restrições orçamentárias e a quantidade de servidores conseguirão gerenciar este processo? A obrigatoriedade e o tempo disponível para a implementação são oportunidades ou desafio?"),_c('br'),_vm._v(" Nesse contexto, a palestra apresenta a experiência da Equipe da UFSC na construção e execução da metodologia do Projeto PlanMob, que auxiliou na estruturação dos planos de mobilidade em oito municípios de pequeno porte simultaneamente. Enaltecendo a necessidade de vinculação entre políticas de transporte e mobilidade destes municípios do nordeste catarinense em decorrência dos fluxos resultantes pela interdependência da infraestrutura viária disponível. A percepção das causas e consequências à construção de diretrizes de desenvolvimento e de planejamento urbano de forma conjunta."),_c('br'),_vm._v(" A sistematização das atividades foi dividida em oito etapas. A primeira enfatiza a estruturação político-executiva do Município necessárias; as seis etapas subsequentes elucidam o desenvolvimento do Plano de Mobilidade Urbana; e a última etapa indica à elaboração da Carta de Intenções do Plano de Mobilidade Regional."),_c('br'),_vm._v(" A metodologia proposta pela Equipe UFSC oportunizou a realização de palestras e capacitações técnicas para o uso de ferramentas e técnicas de planejamento e monitoramento da mobilidade urbana. Além do estímulo da participação direta da população e de seus representantes na formulação, execução e acompanhamento das atividades para ampliar a consolidação do PMU."),_c('br'),_vm._v(" Alguns desafios para a equipe UFSC foram o tempo requerido para a execução do projeto; o desenvolvimento simultâneos das etapas nos municípios; e a participação como agentes executores de seus próprios Planos de Mobilidade."),_c('br'),_vm._v(" O objetivo da condução do processo foi viabilizar a elaboração do Plano de Mobilidade Urbana, e principalmente, permitir que a revisão, continuidade e integração com o planejamento urbano seja realizada pelos municípios com autonomia em qualquer época."),_c('br'),_vm._v(" A consolidação da proposta evidencia a prioridade aos transportes ativos, vinculados aos transportes coletivo e escolar para o planejamento do sistema viário e tráfego. O preceito de identificar o uso e a ocupação do solo, preocupando-se com o meio ambiente e patrimônio, conduziu a análise dos deslocamentos não motorizados como atividade cotidiana necessária e salutar ao ser humano. "),_c('br')])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/andrea_holz.jpg"),"width":"160px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: Andrea Holz Pfutzenreuter"),_c('br'),_c('br'),_vm._v("Professora adjunta na Universidade Federal de Santa Catarina, UFSC Campus Joinville. Doutora em Arquitetura e Urbanismo pela Universidade Mackenzie/SP, participou na Friedrich-Schiller-Universität em Jena-Alemanha do grupo de pesquisa em Geografia Humana. Especialista em Gerontologia pela Sociedade Brasileira de Geriatria e Gerontologia (SBGG). Mestre em Habitação com ênfase em Planejamento, Gestão e Projeto pelo Instituto de Pesquisa Tecnológicas de São Paulo (IPT 2008), especialista em Gerontologia (UFSC 2010) e em Gestão de Projetos Sociais (SENAC-SP 2006). ")])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/simone_becker.jpg"),"width":"160px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: Simone Becker Lopes"),_c('br'),_c('br'),_vm._v("Arquiteta e Urbanista e Mestre e Doutora em Planejamento e Operação de Sistemas de Transportes pela USP – São Carlos, com Doutorado Sanduíche no Institute of Transportation da Vienna University of Technology. Trabalhou por 10 anos na EPTC (Empresa Pública de Transportes e Circulação de Porto Alegre) com simulação de tráfego, pesquisas e análise de dados de mobilidade e SIG (Sistemas de Informações Geográficas). Tem experiência em consultorias para administrações públicas municipais, na área de planos de transportes e mobilidade e de gerenciamento de infraestrutura urbana, assim como para administração pública federal como consultora em estudos de tráfego, através da ONU, para atuar no PNUD (Plano das Nações Unidas para o Desenvolvimento) de 2014 a 2015. Desde 2015, é professora na UFSC CT Joinville na área de Planejamento de Transportes e Mobilidade, com disciplinas e pesquisa ligadas a Planejamento Integrado de Transportes e Uso do Solo, Análise de Demanda e Sistemas de Informações Geográficas. "),_c('br')])],1),_c('v-row',{attrs:{"justify":"center"}},[_c('v-col',{attrs:{"align":"center"}},[_c('v-img',{attrs:{"src":require("../assets/img/renata_cavion.jpg"),"width":"140px"}})],1)],1),_c('v-row',{staticStyle:{"text-align":"justify"},attrs:{"justify":"start","no-gutters":""}},[_c('v-col',{attrs:{"cols":"24"}},[_vm._v(" Currículo: Renata Cavion"),_c('br'),_c('br'),_vm._v("A