import VueI18n from 'vue-i18n';
import Vue from 'vue';
import EN from './en';
import ES from './es';
import PT from './pt';

Vue.use(VueI18n);

const messages = {
  pt: PT,
  en: EN,
  es: ES,
};

const i18n = new VueI18n({
  locale: 'pt', // set locale
  messages, // set locale messages
});

export default i18n;
