                    style="text-align: justify"
                  >
                      <v-col
                        cols="24"
                      >
                        <br>Quais ações municipais são necessárias para viabilizar a elaboração
                         dos Planos de Mobilidade Urbana (PMU)? As restrições orçamentárias e a
                         quantidade de servidores conseguirão gerenciar este processo? A
                         obrigatoriedade e o tempo disponível para a implementação são
                         oportunidades ou desafio?<br>
                        Nesse contexto, a palestra apresenta a experiência da Equipe da UFSC na
                         construção e execução da metodologia do Projeto PlanMob, que auxiliou
                         na estruturação dos planos de mobilidade em oito municípios de pequeno
                         porte simultaneamente. Enaltecendo a necessidade de vinculação entre
                         políticas de transporte e mobilidade destes municípios do nordeste
                         catarinense em decorrência dos fluxos resultantes pela interdependência
                         da infraestrutura viária disponível. A percepção das causas e
                         consequências à construção de diretrizes de desenvolvimento e de
                         planejamento urbano de forma conjunta.<br>
                        A sistematização das atividades foi dividida em oito etapas. A primeira
                         enfatiza a estruturação político-executiva do Município necessárias;
                         as seis etapas subsequentes elucidam o desenvolvimento do Plano de
                         Mobilidade Urbana; e a última etapa indica à elaboração da Carta de
                         Intenções do Plano de Mobilidade Regional.<br>
                        A metodologia proposta pela Equipe UFSC oportunizou a realização de
                         palestras e capacitações técnicas para o uso de ferramentas e técnicas
                         de planejamento e monitoramento da mobilidade urbana. Além do estímulo
                         da participação direta da população e de seus representantes na
                         formulação, execução e acompanhamento das atividades para ampliar a
                         consolidação do PMU.<br>
                        Alguns desafios para a equipe UFSC foram o tempo requerido para a
                         execução do projeto; o desenvolvimento simultâneos das etapas nos
                         municípios; e a participação como agentes executores de seus
                         próprios Planos de Mobilidade.<br>
                        O objetivo da condução do processo foi viabilizar a elaboração do
                         Plano de Mobilidade Urbana, e principalmente, permitir que a
                         revisão, continuidade e integração com o planejamento urbano seja
                         realizada pelos municípios com autonomia em qualquer época.<br>
                        A consolidação da proposta evidencia a prioridade aos transportes
                         ativos, vinculados aos transportes coletivo e escolar para o
                         planejamento do sistema viário e tráfego. O preceito de
                         identificar o uso e a ocupação do solo, preocupando-se com o meio
                         ambiente e patrimônio, conduziu a análise dos deslocamentos não
                         motorizados como atividade cotidiana necessária e salutar ao ser humano.
